import Vue from 'vue';
import Router from 'vue-router';
import auth from "@/auth/authService";

Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior () {
    return { x: 0, y: 0 };
  },
  routes: [

    {
    // =============================================================================
    // MAIN LAYOUT ROUTES requiresAuth: true
    // =============================================================================
      path: '',
      component: () => import('./layouts/main/Main.vue'),
      meta: {
        authRequired: true
      },
      children: [
      // =============================================================================
      // Theme Routes
      // =============================================================================
        {
          path: '/',
          name: 'home',
          component: () => import('./views/Home.vue'),
          meta: {
            breadcrumb: [
              { title: 'Dashboard', url: '/' },
            ],
            pageTitle: 'Dashboard'
          },
        },
        {
          path: '/employees',
          name: 'employees',
          component: () => import('./views/employees/DataListListView.vue'),
          meta: {
            breadcrumb: [
              { title: 'Nhn vin', url: '/' },
            ],
            pageTitle: 'Nhn vin',
            authRequired: true,
          },
        },
        {
          path: '/employees/:employee',
          name: 'employee',
          component: () => import('./views/employees/DetailEmployee.vue'),
          meta: {
            breadcrumb: [
              { title: 'Chi tit nhn vin', url: '/'}
            ],
            pageTitle: 'Chi tit nhn vin'
          }
        },
        {
          path: '/students',
          name: 'students',
          component: () => import('./views/students/StudentsList.vue'),
          meta: {
            breadcrumb: [
              { title: 'Hc vin', url: '/'}
            ],
            pageTitle: 'Hc vin'
          }
        },
        {
          path: '/students/:student',
          name: 'student',
          component: () => import('./views/students/DetailStudent.vue'),
          meta: {
            breadcrumb: [
              { title: 'Chi tit hc vin', url: '/'}
            ],
            pageTitle: 'Chi tit hc vin'
          }
        },
        {
          path: '/programs',
          name: 'programs',
          component: () => import('./views/programs/ProgramsList.vue'),
          meta: {
            breadcrumb: [
              { title: 'Chng trnh hc', url: '/'}
            ],
            pageTitle: 'Chng trnh hc'
          }
        },
        {
          path: '/settings',
          component: () => import('./views/settings/Settings.vue'),
          meta: {
            breadcrumb: [
              { title: 'Ci t', url: '/'}
            ],
            pageTitle: 'Ci t'
          },
          children: [
            {
              path: '',
              name: 'SettingsSystems',
              component: () => import('./views/settings/systems/SettingsSystems.vue'),
              meta: {
                breadcrumb: [
                  { title: 'Ci t h thng', url: '/'}
                ],
                pageTitle: 'Ci t h thng'
              }
            },
            {
              path: 'branches',
              name: 'SettingsBranches',
              component: () => import('./views/settings/branches/DataListListView.vue'),
              meta: {
                breadcrumb: [
                  { title: 'Ci t chi nhnh', url: '/'}
                ],
                pageTitle: 'Ci t chi nhnh'
              }
            },
            {
              path: 'coupons',
              name: 'SettingsCoupons',
              component: () => import('./views/settings/coupons/CouponsList.vue'),
              meta: {
                breadcrumb: [
                  { title: 'Khuyn mi', url: '/'}
                ],
                pageTitle: 'Khuyn mi'
              }
            },
          ]
        },
        {
          path: '/courses',
          name: 'courses',
          component: () => import('./views/courses/DataListListView.vue'),
          meta: {
            breadcrumb: [
              {title: 'Lp hc ', url: '/'}
            ],
            pageTitle: 'Lp hc'
          }
        },
        {
          path: '/courses/:course',
          name: 'detailcourses',
          component: () => import('./views/courses/DetailCourses.vue'),
          meta: {
            breadcrumb: [
              { title: 'Chi tit lp hc', url: '/'}
            ],
            pageTitle: 'Chi tit lp hc'
          }
        },
      ],
    },
    // =============================================================================
    // FULL PAGE LAYOUTS requiresAuth: false
    // =============================================================================
    {
      path: '',
      component: () => import('@/layouts/full-page/FullPage.vue'),
      children: [
      // =============================================================================
      // PAGES
      // =============================================================================
        {
          path: '/pages/login',
          name: 'pageLogin',
          component: () => import('@/views/pages/Login.vue')
        },
        {
          path: '/pages/error-404',
          name: 'pageError404',
          component: () => import('@/views/pages/Error404.vue')
        },
      ],
      meta: {
        authRequired: false,
      }
    },
    // Redirect to 404 page, if no match found
    {
      path: '*',
      redirect: '/pages/error-404'
    }
  ],
});

router.afterEach(() => {
  const appLoading = document.getElementById('loading-bg');
  if (appLoading) {
    appLoading.style.display = "none";
  }
});

router.beforeEach(async (to, from, next) => {
  if (to.matched.some(m => m.meta.authRequired)) {
    if (await auth.isAuthenticated()) {
      return next();
    }
    else {
      return router.push({ path: '/pages/login', query: { to: to.path } });
    }
  } else {
    return next();
  }
});

export default router;
